@import url(https://fonts.googleapis.com/css2?family=Fira+Sans+Condensed:wght@500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Fira Sans Condensed', sans-serif;
  color: dimgrey;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ServiceApp {
  padding: 20px;
}

#serviceFilter {
  width: 100%;
  padding: 10px;
  border: 1px solid #c4c4c5;
}

.ServiceAdd {
  margin-top: 30px;
}

.ServiceAddInput {
  border: 1px solid #c4c4c5;
  padding: 10px;
}

.ServiceAddInput:nth-child(n + 2) {
  margin-left: 20px;
}

.ServiceAddButton {
  border: 1px solid #c4c4c5;
  padding: 10px;
  margin-left: 20px;
  cursor: pointer;
}

.ServiceList {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  border: 1px solid #c4c4c5;
}

.ServiceList:last-child {
  border-bottom: none;
}

.ServiceWrapperValue {
  display: flex;
  justify-content: space-between;
  width: 50%;
}

.ServiceItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  width: 100%;
  border-bottom: 1px solid #c4c4c5;
  padding: 10px;
}

.ServiceButton {
  border: 1px solid #c4c4c5;
  padding: 5px 7px;
  margin-left: 20px;
  cursor: pointer;
  font-size: 1.2rem;
}

